function ReactSelect({ selectedOption, handleChange, options, title }) {
  return (
    <>
      <Select
        value={selectedOption}
        onChange={handleChange}
        options={options}
        isClearable={false}
        placeholder={title}
        classNamePrefix="react-prefix"
        className={`react-select ${
          selectedOption?.color ? selectedOption?.color : ""
        }`}
        isSearchable={false}
        // menuIsOpen={true}
        // styles={customStyles}
      />
    </>
  );
}

export default ReactSelect;
